import $uni from '../../utils/uni/api.js';

export default {
	props: {
		PageCur: {
			type: String,
			default: 'index'
		}
	},
	
	computed: {
		// 图片路径
		imgSrc() {
			return type => {
				const pageCur = this.PageCur,
					str = pageCur == type ? '_cur' : '';

				return `/static/tabbar/${type + str}.png`;
			};
		}
	},

	methods: {
		// 切换页面
		redirect(e) {
			const PageCur = e.currentTarget.dataset.cur;

			$uni.route.redirectTo(`/pages/${PageCur}/Index`);
		}
	}
};
